import { useEffect, useRef, useState } from "react";

export const ifFalsy = (value: unknown): boolean => {
  return value === 0 ? false : !value;
};

export const isVoid = (value: unknown): boolean => {
  return value === undefined || value === null || value === "" ? true : false;
};

export const cleanObject = (object: { [key: string]: unknown }) => {
  const result = { ...object };
  Object.keys(result).forEach((key) => {
    const value = result[key];
    if (isVoid(value)) {
      delete result[key];
    }
  });
  return result;
};

export const useMount = (callback: () => void) => {
  useEffect(() => {
    callback();
    // eslint-disable-next-line
  }, []);
  //1. 没有可选参数 --- 等价于 componentDidMount 和 componentDidUpdate
  //2. [] --- 代表只在 componetDidMount 时执行
  //3. [xxx] --- 代表在 xxx 发生变化时候会自动触发 effect 里面的方法，如果 xxx 无变化，则 effect 里的方法不作用
};

export const useDebounce = (value: any, delay?: number) => {
  const [debounceValue, setDebounceValue] = useState(value);

  useEffect(() => {
    const timeout = setTimeout(() => {
      setDebounceValue(value);
    }, delay);
    return () => {
      clearTimeout(timeout);
    };
  }, [value, delay]);
  return debounceValue;
};

export const useArray = <T>(array: T[]) => {
  const [value, setValue] = useState(array);
  const add = (object: T) => {
    setValue([object, ...value]);
  };
  const clear = () => {
    setValue([]);
  };
  const removeIndex = (index: number) => {
    const result = [...value];
    result.splice(index, 1);
    setValue(result);
  };
  return { value, add, clear, removeIndex };
};

export const useDocumentTitle = (
  title: string,
  keepOnUnmount: boolean = true
) => {
  const oldTitle = useRef(document.title).current;
  useEffect(() => {
    document.title = title;
  }, [title]);

  useEffect(() => {
    return () => {
      if (!keepOnUnmount) {
        document.title = oldTitle;
      }
    };
  }, [keepOnUnmount, oldTitle]);
};

export const resetRouter = () => {
  window.location.href = window.location.origin;
};

export const useMountedRef = () => {
  const mountedRef = useRef(false);
  useEffect(() => {
    mountedRef.current = true;
    return () => {
      mountedRef.current = false;
      // console.log("组件卸载");
    };
  });
  return mountedRef;
};
