type State<T> = {
  past: T[];
  present: T;
  future: T[];
};
type Action<T> = {
  newPresent?: T;
  type: "SET" | "RESET" | "UNDO" | "REDO";
};

const undoReducer = <T>(state: State<T>, action: Action<T>) => {
  const { past, present, future } = state;
  const { newPresent } = action;
  switch (action.type) {
    case "SET": {
      return {
        past: [...past, present],
        present: newPresent,
        future: [],
      };
    }
    case "RESET": {
      return {
        past: [],
        present: newPresent,
        future: [],
      };
    }
    case "UNDO": {
      return {
        past: past.slice(0, -1),
        present: past.slice(-1)[0],
        future: [...future, present],
      };
    }
    case "REDO": {
      return {
        past: [...past, present],
        present: future.slice(-1)[0],
        future: future.slice(0, -1),
      };
    }
    default: {
      return state;
    }
  }
};

export const useUndo = <T>(initPresent: T) => {
  const [state, dispatch] = useReducer(undoReducer, {
    past: [],
    present: initPresent,
    future: [],
  } as State<T>);

  const canUndo = state.past.length > 0 ? true : false;
  const canRedo = state.future.length > 0 ? true : false;

  const set = useCallback((newPresent: T) => {
    dispatch({ type: "SET", newPresent });
  }, []);

  const reSet = useCallback((newPresent: T) => {
    dispatch({ type: "RESET", newPresent });
  }, []);

  const undo = useCallback(() => {
    dispatch({ type: "UNDO" });
  }, []);

  const redo = useCallback(() => {
    dispatch({ type: "REDO" });
  }, []);

  return [
    state,
    {
      set,
      reSet,
      undo,
      redo,
      canRedo,
      canUndo,
    },
  ] as const;
};
