import { useCallback, useReducer, useState } from "react";
import { useMountedRef } from "utils";

interface State<D> {
  error: Error | null;
  data: D | null;
  status: "idle" | "loding" | "error" | "success";
}

const defaultInitailState: State<null> = {
  error: null,
  data: null,
  status: "idle",
};

const defaultConfig = {
  throwOnError: false,
};

const useSafeDiapatch = <T>(diapatch: (...args: T[]) => void) => {
  const mountedRef = useMountedRef();
  return useCallback(
    (...args: T[]) => {
      mountedRef.current ? diapatch(...args) : void 0;
    },
    [diapatch, mountedRef]
  );
};

export const useAsync = <D>(
  initialState?: State<D>,
  initailConfig?: typeof defaultConfig
) => {
  const [state, dispatch] = useReducer(
    (state: State<D>, action: Partial<State<D>>) => {
      return { ...state, ...action };
    },
    {
      ...defaultInitailState,
      ...initialState,
    }
  );
  const [retry, setRetry] = useState<() => () => void>(() => () => {});
  const config = { ...defaultConfig, ...initailConfig };
  const safeDispatch = useSafeDiapatch(dispatch);

  const setDate = useCallback(
    (data: D) => {
      safeDispatch({
        data,
        error: null,
        status: "success",
      });
    },
    [safeDispatch]
  );

  const setError = useCallback(
    (error: Error | null) => {
      safeDispatch({
        error,
        data: null,
        status: "error",
      });
    },
    [safeDispatch]
  );

  const run = useCallback(
    (promise: Promise<D>, retryConfig?: { retry: () => Promise<D> }) => {
      if (!promise || !promise.then) {
        throw new Error("请传入promsie类型数据");
      }

      setRetry(() => () => {
        if (retryConfig?.retry) run(retryConfig?.retry(), retryConfig);
      });

      safeDispatch({ status: "loding" });

      return promise
        .then((data) => {
          setDate(data);
          return data;
        })
        .catch((error) => {
          setError(error);
          if (config.throwOnError) {
            return Promise.reject(error);
          }
          return error;
        });
    },
    [config.throwOnError, setDate, setError, safeDispatch]
  );

  return {
    isIdle: state.status === "idle" ? true : false,
    isLoading: state.status === "loding" ? true : false,
    isError: state.status === "error" ? true : false,
    isSuccess: state.status === "success" ? true : false,
    setDate,
    setError,
    run,
    retry,
    ...state,
  };
};
