import React, { ErrorInfo, ReactElement, ReactNode } from "react";
interface PropsType {
  children: ReactNode;
  failbackRender: ({
    error,
    info,
  }: {
    error: Error | null;
    info?: ErrorInfo | undefined;
  }) => ReactElement;
}

export class ErrorBoundary extends React.Component<
  PropsType,
  { error: Error | null; info?: ErrorInfo | undefined }
> {
  constructor(props: PropsType) {
    super(props);
    this.state = {
      error: null,
    };
  }

  // static getDerivedStateFromError(error:Error|null){
  //   return{error}
  // }

  componentDidCatch(error: Error | null, info: ErrorInfo) {
    this.setState({
      error,
      info,
    });
  }

  render() {
    const { error, info } = this.state;
    const { failbackRender, children } = this.props;
    if (error) {
      console.log("error", info);
      return failbackRender({ error, info });
    }
    return children;
  }
}
