import { Button, Dropdown, Menu } from "antd";
import { useAuth } from "context/auth-context";
import { ProjectListScreen } from "screens/project-list";
import styled from "@emotion/styled";
import { ReactComponent as SoftwareLogo } from "./assets/software-logo.svg";
import { resetRouter, useDocumentTitle } from "utils";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import { ProjectScreen } from "screens/project";
import { ProjectModal } from "screens/project-list/project-modal";
import { ProjectPopover } from "components/project-popover";
import { Row } from "components/lib";

/*
 * @Date: 2021-06-04 09:30:07
 * @LastEditors: wangyifeng
 * @LastEditTime: 2021-06-16 09:42:25
 * @Description:
 */
export const AuthenticatedAPP = () => {
  const { logout } = useAuth();
  useDocumentTitle("项目列表", false);
  const menu = (
    <Menu>
      <Menu.Item key="logout">
        <Button
          type="link"
          onClick={() => logout({ username: "", password: "" })}
        >
          登出
        </Button>
      </Menu.Item>
      <Menu.Item key="welcome">
        <Button type="link" onClick={() => alert("welcome")}>
          欢迎！
        </Button>
      </Menu.Item>
    </Menu>
  );

  const PageHeader = () => {
    const { user } = useAuth();
    return (
      <Header between={true}>
        <HeaderLeft gap={true}>
          <LogoButton type="link" onClick={resetRouter}>
            <SoftwareLogo width="18rem" color={"rgb(38,132,255)"} />
          </LogoButton>
          <ProjectPopover />
          <span>用户</span>
        </HeaderLeft>
        <HeaderRight>
          <Dropdown overlay={menu} arrow>
            <Button type="link" onClick={(e) => e.preventDefault()}>
              Hi, {user?.name}
            </Button>
          </Dropdown>
        </HeaderRight>
      </Header>
    );
  };

  return (
    <Container>
      <Router>
        <PageHeader />
        <Main>
          <Routes>
            <Route path="/projects" element={<ProjectListScreen />}></Route>
            <Route
              path="/projects/:projectId/*"
              element={<ProjectScreen />}
            ></Route>
            <Navigate to="/projects" /> 
          </Routes>
        </Main>
        <ProjectModal />
      </Router>
    </Container>
  );
};

const Container = styled.div`
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
`;

const Header = styled(Row)`
  height: 6rem;
  padding: 3.2rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0 0 5px 0;
`;
const HeaderLeft = styled(Row)``;
const HeaderRight = styled.div``;
const Main = styled.main`
  flex: 1;
  display: flex;
  overflow: hidden;
`;

const LogoButton = styled(Button)`
  display: flex;
  align-items: center;
  padding: 0;
`;
