import { List } from "./list";
import { SearchPanel } from "./search-panel";
import { useDebounce } from "utils";
import { useProjects } from "utils/project";
import { useUsers } from "utils/user";
import { useProjectModal, useProjectSearchParam } from "./util";
import {
  ButtonNoPadding,
  ErrorBox,
  Row,
  ScreenContainer,
} from "components/lib";
import { useUndo } from "utils/use-undo";

export const ProjectListScreen = () => {
  const [param, setParam] = useProjectSearchParam();
  const { isLoading, error, data: list } = useProjects(useDebounce(param, 200));
  const { data: users } = useUsers();
  const [state, { set, reSet, undo, redo, canUndo, canRedo }] = useUndo(0);
  const { open } = useProjectModal();
  const { present } = state;

  return (
    <ScreenContainer>
      <div>
        <div> count number:{present}</div>
        <div>
          <button onClick={() => set(Number(present) + 1)}>+</button>
          <button onClick={() => set(Number(present) - 1)}>-</button>
          <button onClick={() => undo()} disabled={!canUndo}>
            undo
          </button>
          <button onClick={() => redo()} disabled={!canRedo}>
            redo
          </button>
          <button onClick={() => reSet(0)}>reset to 0</button>
        </div>
      </div>
      <Row between={true}>
        <h1>项目列表</h1>
        <ButtonNoPadding
          type="link"
          onClick={() => {
            open();
          }}
        >
          创建项目
        </ButtonNoPadding>
      </Row>
      <SearchPanel users={users || []} param={param} setParam={setParam} />
      <ErrorBox error={error}></ErrorBox>
      <List
        users={users || []}
        dataSource={list || []}
        loading={isLoading}
      ></List>
    </ScreenContainer>
  );
};

ProjectListScreen.whyDidYouRender = true;
