import { Dropdown, Menu, Modal, Table, TableProps } from "antd";
import dayjs from "dayjs";
import { User } from "../../type/user";
import { Link } from "react-router-dom";
import { Pin } from "components/pin";
import { useDeleteProject, useEditProject } from "utils/project";
import { ButtonNoPadding } from "components/lib";
import { useProjectModal, useProjectsQueryKey } from "./util";
import { Project } from "../../type/project";

interface ListProps extends TableProps<Project> {
  users: User[];
}

export const List = ({ users, ...restProps }: ListProps) => {
  const { mutate } = useEditProject(useProjectsQueryKey());
  const pinProject = (id: number) => (pin: boolean) => mutate({ id, pin });
  return (
    <Table
      pagination={false}
      rowKey={(record) => record.name}
      columns={[
        {
          title: () => <Pin checked={true} disabled />,
          dataIndex: "pin",
          render(value, project) {
            return (
              <Pin
                checked={project.pin}
                oncheckedChange={pinProject(project.id)}
              />
            );
          },
        },
        {
          title: "名称",
          dataIndex: "name",
          sorter: (a, b) => a.name.localeCompare(b.name),
          render(value, record) {
            return <Link to={record.id.toString()}>{value}</Link>;
          },
        },
        {
          title: "部门",
          dataIndex: "organization",
        },
        {
          title: "负责人",
          dataIndex: "personId",
          render(value, record) {
            return (
              <span>
                {" "}
                {users.find((user) => user.id === value)?.name ?? "未知"}
              </span>
            );
          },
        },
        {
          title: "创建时间",
          dataIndex: "created",
          render(value, record) {
            return value ? dayjs(value).format("YYYY-MM-DD") : "无";
          },
        },
        {
          render(value, record) {
            return <More project={record} />;
          },
        },
      ]}
      {...restProps}
    />
  );
};

const More = ({ project }: { project: Project }) => {
  const { startEdit } = useProjectModal();
  const editProject = (id: number) => startEdit(id);

  const { mutate: deleteProject } = useDeleteProject();
  const confirmDeleteProject = (id: number) => {
    Modal.confirm({
      title: "确认删除本项目吗？",
      content: "点击确定删除项目",
      okText: "确定",
      cancelText: "取消",
      onOk: () => {
        deleteProject(id);
      },
    });
  };

  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item
            key="edit"
            onClick={() => {
              editProject(project.id);
            }}
          >
            编辑项目
          </Menu.Item>
          <Menu.Item
            key="delete"
            onClick={() => {
              confirmDeleteProject(project.id);
            }}
          >
            删除项目
          </Menu.Item>
        </Menu>
      }
    >
      <ButtonNoPadding type="link">...</ButtonNoPadding>
    </Dropdown>
  );
};
