import { QueryKey, useMutation, useQuery, useQueryClient } from "react-query";
import { Task } from "type/task";
import { useDebounce } from "utils";
import { useHttp } from "./http";

/*
 * @Date: 2021-08-02 19:36:47
 * @LastEditors: wangyifeng
 * @LastEditTime: 2021-08-04 14:17:53
 * @Description:
 */
export const useTasks = (param?: Partial<Task>) => {
  const client = useHttp();
  const debouncedParam = { ...param, name: useDebounce(param?.name, 200) };
  return useQuery<Task[], Error>(["tasks", debouncedParam], () =>
    client("tasks", { data: debouncedParam })
  );
};

export const useTask = (id?: number) => {
  const client = useHttp();
  return useQuery<Task>(["task", id], () => client(`tasks/${id}`), {
    enabled: !!id,
  });
};

export const useAddTask = (queryKey: QueryKey) => {
  const client = useHttp();
  const queryClient = useQueryClient();
  return useMutation(
    (param: Partial<Task>) =>
      client(`tasks`, {
        data: param,
        method: "POST",
      }),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(queryKey);
      },
    }
  );
};

export const useEditTask = (queryKey: QueryKey) => {
  const client = useHttp();
  const queryClient = useQueryClient();
  return useMutation(
    (param: Partial<Task>) =>
      client(`tasks/${param.id}`, { data: param, method: "PATCH" }),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(queryKey);
      },
    }
  );
};

export const useDeleteTask = (queryKey: QueryKey) => {
  const client = useHttp();
  const queryClient = useQueryClient();
  return useMutation(
    (id: number) =>
      client(`tasks/${id}`, {
        method: "DELETE",
      }),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(queryKey);
      },
    }
  );
};
