import { Button, Card, Divider } from "antd";
import { useState } from "react";
import { LoginScreen } from "./login";
import { RegisterScreen } from "./register";
import styled from "@emotion/styled";
import logo from "../assets/logo.svg";
import left from "../assets/left.svg";
import right from "../assets/right.svg";
import { useDocumentTitle } from "utils";
import { ErrorBox } from "components/lib";

/*
 * @Date: 2021-06-04 09:36:17
 * @LastEditors: wangyifeng
 * @LastEditTime: 2021-07-13 15:45:02
 * @Description:
 */
export const UnAuthenticatedAPP = () => {
  const [ifRegister, setIfRegister] = useState(true);
  const [error, setError] = useState<Error | null>(null);
  useDocumentTitle("请登录或注册以继续", false);
  return (
    <Container>
      <Header />
      <Background />
      <ShadowCard>
        <Title>{ifRegister ? "请登录" : "请注册"}</Title>
        <ErrorBox error={error}></ErrorBox>
        {ifRegister ? (
          <LoginScreen onError={setError} />
        ) : (
          <RegisterScreen onError={setError} />
        )}
        <Divider></Divider>
        <Button
          type="link"
          onClick={() => {
            setError(null);
            setIfRegister(!ifRegister);
          }}
        >
          {ifRegister ? "没有账号吗？注册新账号" : "已有账号啦！立即登录"}
        </Button>
      </ShadowCard>
    </Container>
  );
};

const Title = styled.h2`
  color: rgb(94, 108, 132);
  margin-bottom: 2.4rem;
`;

const Background = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(${left}), url(${right});
  background-position: left bottom, right bottom;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: calc(((100vw - 40rem) / 2) - 3.2rem),
    calc(((100vw - 40rem) / 2) - 3.2rem), cover;
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ShadowCard = styled(Card)`
  width: 40rem;
  min-height: 56rem;
  padding: 3.2rem 4rem;
  border-radius: 0.3rem;
  box-sizing: border-box;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
`;

const Header = styled.header`
  background: url(${logo}) no-repeat center;
  padding: 5rem 0;
  background-size: 8rem;
  width: 100%;
`;

export const LongButton = styled(Button)`
  width: 100%;
`;
